import React from 'react';


export class AnnounsmentItem extends React.Component {

    render() {
        return (
            <div className="Add">

                <ul><li>
                    <h4>{this.props.announcements[0].title}</h4>
                    <p>{this.props.announcements[0].description}</p>
                    <span>{this.props.announcements[0].date}</span>
                </li>
                    <li>
                        <h4>{this.props.announcements[1].title}</h4>
                        <p>{this.props.announcements[1].description}</p>
                        <span>{this.props.announcements[1].date}</span>
                    </li>
                </ul>
            </div>
        );
    }
}
